@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap');
.loading{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    width: 100%;
    z-index: 99;
    background: #070A52;
    display: grid;
    place-items: center;
    font-family: 'Josefin Sans', sans-serif;
    &__box{
        position: relative;
        width: 500px;
        height: 200px;
        border: 3px solid #6cff8d;
        border-top: 3px solid #6cff8c7a;
        border-bottom: 3px solid #6cff8c7a;
    }
    &__bar{
        width: 90%;
        height: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%);
        background: #ccc;
        border-radius: 2px;
        &--inner{
            height: 100%;
            width: 50%;
            border-radius: 2px;
            background: #6cff8d;
        }
    }

    &__text{
        position: relative;
        color: #fff;
        padding: 1rem;
        font-size: 20px;
        font-weight: 700;
        display: flex;
        align-items: center;
        justify-content: center;
        &--dot{
            width: 15px;
            height: 15px;
            margin: 0 3px;
            border-radius: 50%;
            background: #fff;
            animation: pulse 1s infinite;
            @keyframes pulse {
                from{
                    opacity: 0;
                    background: #6cff8d;
                } to{
                    opacity: 1;
                }
                
            }
        }
    }
}

